body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  height: 100vh;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  color: #1a1b15;
  text-align: center;
  padding: 1rem;
  margin: 0;
}

h1 {
  font-size: 4rem;
  font-family: 800;
}

h2 {
  font-size: 3.4rem;
  font-weight: 600;
}

h3 {
  font-size: 3rem;
  text-align: center;
  font-weight: 400;
}

p {
  color: #1a1b15;
  font-size: 2rem;
}

.inicio {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh; /* Altura mínima del viewport */
  height: 100%;
  margin: 1rem 5rem 2rem 5rem;
}

.titulo {
  color: #003559;
  font-size: 4.8rem;
  text-shadow: 1px 2px 2px #1a1b15;
}
.titulo span {
  padding: 1rem;
  font-size: 3.4rem;
}

.contenedor-inicio {
  border-radius: 1rem;
  border: 2px solid #00b4d8;
  text-align: center;
  height: auto;
  width: 45rem;
  margin: 2rem;
  padding: 3rem;
}

@media (min-width: 768px) {
  .contenedor-inicio {
    width: 60rem;
  }
}
.opciones {
  display: flex;
  flex-direction: column;
}

.boton {
  background-color: #003559;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 1rem 0;
  border-radius: 1.5rem;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #001726;
  cursor: pointer;
}

.ir-inicio a {
  color: #1a1b15;
}

.acciones {
  width: 100%;
}

.clase {
  color: #003559;
  font-weight: 600;
}

.formulario {
  width: 100%;
  align-items: center;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
  text-align: left;
}
.campo label {
  flex: 0 0 10rem;
  padding: 1rem;
}
.campo input, .campo select {
  text-align: center;
  width: 100%;
  flex: 1;
  border: 1px solid #003559;
  padding: 1.25rem;
  border-radius: 1rem;
}

.boton {
  background-color: #003559;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 1rem 0;
  border-radius: 1.5rem;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #001726;
  cursor: pointer;
}

.olvide a {
  font-size: 1.8rem;
  font-weight: 600;
  color: #1a1b15;
}
.olvide a:hover {
  color: #003559;
}

.acciones {
  margin-top: 3rem;
  width: 75%;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  color: #1a1b15;
  font-weight: 400;
  display: block;
  margin-bottom: 2.5rem;
  text-align: center;
}

.mensaje {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 4rem;
}
.mensaje span {
  font-weight: bold;
}
.mensaje .acciones {
  margin: 0 5rem;
  width: 80%;
}

.alerta {
  padding: 1.25rem;
  width: 100%;
  color: #FFFFFF;
  font-weight: 600;
  display: block;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 700;
}
.alerta.error {
  background-color: #cb0000;
  border-left: #650000 4px solid;
}
.alerta.exito {
  background-color: #329f00;
  border-left: #123900 4px solid;
}

.dashboard {
  background-color: #e1e1e1;
  width: 100%;
  text-align: center;
}
@media (min-width: 768px) {
  .dashboard {
    position: fixed;
    display: flex;
    height: 100vh;
  }
}
.dashboard .principal {
  flex: 1;
  overflow-y: auto;
}

.menu {
  text-align: center;
  align-items: center;
}

.menu-regresar {
  padding: 0 4rem;
  width: 30rem;
}
@media (min-width: 480px) {
  .menu-regresar {
    padding: 0;
    width: 20rem;
  }
}

.sidebar {
  background-color: #003559;
  padding: 2rem;
}
.sidebar h2 {
  color: #FFFFFF;
}
.sidebar .sidebar-nav {
  margin-top: 1rem;
}
.sidebar .sidebar-nav a {
  color: #FFFFFF;
  display: block;
  margin-bottom: 1rem;
  padding: 2rem;
  border-radius: 0.5rem;
  transition-property: background-color;
  transition-duration: 300ms;
  text-transform: uppercase;
}
.sidebar .sidebar-nav a:hover, .sidebar .sidebar-nav a.activo {
  background-color: #0077b6;
  border-left: 6px solid #00b4d8;
}

.barra {
  background-color: #FFFFFF;
  padding: 1rem 2rem;
  box-shadow: 1px 12px 9px -13px #1a1b15;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.barra span {
  color: #003559;
  font-weight: 600;
  text-transform: uppercase;
  font-style: italic;
}
.barra a {
  color: #1a1b15;
}
.barra .cerrar-sesion {
  background-color: #003559;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 1rem 0;
  font-size: 1.4rem;
  font-weight: 400;
}
@media (min-width: 768px) {
  .barra .cerrar-sesion {
    width: auto;
  }
}
.barra .cerrar-sesion:hover {
  background-color: #001726;
  cursor: pointer;
}
@media (min-width: 480px) {
  .barra .cerrar-sesion {
    padding: 1;
    width: 12rem;
  }
}
@media (min-width: 768px) {
  .barra .cerrar-sesion {
    width: 20rem;
  }
}

.fichas-usuarios {
  padding-left: 3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
@media (min-width: 768px) {
  .fichas-usuarios {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
  }
}
.fichas-usuarios .ficha {
  width: 35rem;
  margin: 3rem;
  padding: 2rem;
  background-color: #FFFFFF;
  border-radius: 1rem;
  border: #0077b6 2px solid;
  align-items: center;
  text-align: center;
}
.fichas-usuarios .ficha h4 {
  text-transform: uppercase;
  font-size: 2.6rem;
}
.fichas-usuarios .ficha .imagen {
  width: 20rem;
  height: auto;
  padding: 0 2rem 2rem 2rem;
}
.fichas-usuarios .ficha .imagen img {
  width: 20rem;
}

.admin {
  padding: 1rem 2rem 3rem 2rem;
  margin: 0 3rem 2rem 3rem;
}
.admin .registro {
  background-color: #FFFFFF;
  width: auto;
  height: auto;
  padding: 1rem 3rem;
  border: #003559 2px solid;
  box-shadow: -4px 12px 9px -5px #1a1b15;
}
@media (min-width: 480px) {
  .admin .registro {
    overflow-x: auto;
  }
}
.admin .registro .acciones-registro {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.admin .registro .acciones-registro .boton-registro {
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-align: center;
}
@media (min-width: 480px) {
  .admin .registro .acciones-registro .boton-registro {
    width: 15rem;
  }
}
.admin .registro .acciones-registro .boton-registro img {
  width: 3rem;
}
@media (min-width: 480px) {
  .admin .registro .acciones-registro .boton-registro img {
    width: 2rem;
  }
}
.admin .registro .acciones-registro .buscador {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.admin .registro .acciones-registro .buscador .buscador-input {
  margin: 0 2rem 0 1.5rem;
  height: 3.5rem;
  width: 40rem;
  border-radius: 1rem;
  padding: 1rem;
  border: 900 3px solid;
}
@media (min-width: 480px) {
  .admin .registro .acciones-registro .buscador .buscador-input {
    width: 25rem;
  }
}
.admin .tabla {
  margin: 1rem 0 1rem 0;
  border: #e1e1e1 1px solid;
  width: 100%;
}
.admin .tabla .encabezado {
  height: 4rem;
  font-size: 1.8rem;
  background: #e1e1e1;
  text-transform: uppercase;
}
.admin .tabla .encabezado th {
  font-weight: bold;
}
.admin .tabla th {
  height: 4rem;
  margin: 1rem;
  font-weight: 200;
}
.admin .tabla .operaciones {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
}
.admin .tabla .operaciones button {
  border: 0px;
  background-color: #FFFFFF;
}
.admin .tabla .operaciones a {
  color: #FFFFFF;
  padding: 2rem;
}
.admin .tabla .operaciones a .editar {
  background-color: #003559;
  width: auto;
  color: #FFFFFF;
}

.contenedor-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1rem;
  padding: 0;
  width: auto;
}
@media (min-width: 768px) {
  .contenedor-form {
    margin: 0 10rem 0 10rem;
    padding: 1rem 15rem;
  }
}
.contenedor-form .formulario .campo {
  justify-content: space-between;
}
.contenedor-form .formulario .campo label {
  font-weight: 600;
}
@media (min-width: 768px) {
  .contenedor-form .formulario .campo label {
    flex: 0 0 20rem;
  }
}
@media (min-width: 480px) {
  .contenedor-form .formulario .campo label {
    flex: 0 0 10rem;
  }
}
.contenedor-form .formulario .campo input, .contenedor-form .formulario .campo select {
  flex: initial;
  width: 60%;
}
.contenedor-form .formulario .opciones {
  margin: 0 15rem;
}
@media (min-width: 480px) {
  .contenedor-form .formulario .opciones {
    margin: 0;
  }
}